@import "../core/variables";
@import "../core/reset";
.form {
  display: flex;
  flex-direction: column;
  column-gap: 10px;
  align-items: flex-end;
  padding: 10px;

  @media (min-width: $breakpoint-tablet) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 40px;
    padding: 20px;
  }

  &--label {
    margin: 0 0 1px 0;
    padding: 5px 5px 5px 0;
    font-size: 1.8rem;
    font-weight: bold;
    color: $orange;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (min-width: $breakpoint-tablet) {
      font-size: 2.4rem;
    }
  }
  &--input__text {
    margin: 2px;
    padding: 3px;
    border-radius: 4px;
    border: 0.8px solid $orange;
    font-size: 1.3rem;
    outline: none;
    background-color: $warm-orange;
    color: $black-80;
    width: 200px;
    @media (min-width: $breakpoint-tablet) {
      font-size: 1.8rem;
      padding: 8px;
      margin: 5px;
      width: 300px;
    }
  }
}
