@import "../core/variables";
@import "../core/reset";
.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: solid 3px $white;
  height: 8vh;
  width: 100%;

  @media (min-width: $breakpoint-tablet) {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 30px;
  }
  &__text {
    color: $orange;
    margin-top: 5px;
    font-family: $fasthand;
    letter-spacing: 1.5px;
    font-size: 2rem;
  }
  &__container {
    display: flex;
  }
  &__logo {
    color: $orange;
    margin: 5px 0 0 10px;
    font-size: 2rem;
    cursor: pointer;
    &:hover {
      transform: scale(140%);
    }
  }
}
