@import "../core/variables";
@import "../core/reset";

.detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  @media (min-width: $breakpoint-tablet) {
    padding: 70px;
  }

  &--button {
    text-decoration: none;
    font-size: 1.8rem;
    margin: 10px;
    color: $white;
    text-align: center;
    @media (min-width: $breakpoint-tablet) {
      font-size: 3.8rem;
    }
  }

  &--article {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $orange;
    max-width: 400px;
    min-width: 290px;
    @media (min-width: $breakpoint-tablet) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      max-width: 900px;
      min-width: 700px;
    }
    @media (min-width: $breakpoint-desktop) {
      max-width: 1000px;
      min-width: 1000px;
    }

    &__img {
      padding: 20px;
      width: 100%;
    }
    &__icon {
      color: $white;
      font-size: 1.2rem;
      padding-left: 5px;
      @media (min-width: $breakpoint-tablet) {
        font-size: 1.9rem;
      }
    }
    &__container {
      display: flex;
      flex-direction: row;
      align-items: flex-start;
      padding: 20px 20px;
      @media (min-width: $breakpoint-tablet) {
        display: flex;
        flex-direction: column;
      }
    }
    &__list {
      display: flex;
      flex-direction: column;
      justify-content: center;
      gap: 10px;
    }
    &__term {
      color: $white;
      list-style: none;
      font-size: 2.4rem;
    }
    &__term.name {
      font-size: 3.5rem;
      color: $white;
    }
    &__span {
      display: flex;
      align-items: flex-end;
      color: $black;
      font-size: 2.2rem;
      padding-left: 60px;
      padding-top: 5px;
      text-transform: capitalize;
    }
    &__span.email {
      text-transform: none;
    }

    &__error {
      grid-column: 1/3;
      color: $white;
      font-size: 2rem;
      padding: 20px;
      margin: 20px;
      display: flex;
      justify-content: center;
    }
  }
}
