@import "../core/variables";
@import "../core/reset";
.user {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;

  &--link {
    text-decoration: none;
  }

  &--li {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 25px;
    text-decoration: none;
    border-radius: 10px;
    background-color: $orange;
    overflow: hidden;
    width: 300px;
    @media (min-width: $breakpoint-tablet) {
      width: 320px;
    }
    @media (min-width: $breakpoint-desktop) {
      width: 380px;
    }
  }

  &--img {
    width: 100%;

    height: 400px;
  }

  &--title {
    color: $white;
    padding: 8px 20px 5px 20px;
    font-size: 1.7rem;
    font-weight: bold;

    @media (min-width: $breakpoint-tablet) {
      font-size: 1.9rem;
    }
    @media (min-width: $breakpoint-desktop) {
      font-size: 2.1rem;
    }
  }
  &--description {
    color: $white;
    padding: 5px 20px 8px 35px;
    font-size: 1.7rem;
    @media (min-width: $breakpoint-tablet) {
      font-size: 1.9rem;
    }
  }
  &--error {
    color: $white;
    font-size: 2.4rem;
    padding: 20px;
    display: flex;
    justify-content: center;

    color: $orange;
    @media (min-width: $breakpoint-tablet) {
      font-size: 2.8rem;
    }
    @media (min-width: $breakpoint-desktop) {
      font-size: 3rem;
    }
  }
}
