@import "../core/variables";
@import "../core/reset";
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 15vh;
  width: 100%;
  border-bottom: solid 3px $white;
  &--image {
    padding: 20px;
    width: 120px;
    height: 120px;
    @media (min-width: $breakpoint-tablet) {
      width: 150px;
      height: 150px;
    }
  }
  &--title {
    padding: 10px;
    font-size: 3rem;
    text-transform: uppercase;
    color: $orange;
    @media (min-width: $breakpoint-tablet) {
      font-size: 3.5rem;
    }
  }
}
